import React, { useState } from 'react';
import { StaticQuery, graphql } from 'gatsby';
import Img from 'gatsby-image';
//import { ParallaxProvider } from 'react-scroll-parallax';
//import { Parallax } from 'react-scroll-parallax';\
import Fade from 'react-reveal/Fade';
import styled from 'styled-components';

import {Section, Container} from '@components/global';
import SectionHeader from '@ui/sectionheader';
import step1 from '@images/art/step_1.svg';
import step2 from '@images/art/step_2.svg';
import step3 from '@images/art/step_3.svg';

const STEPS = [
  {
    header: 'Connect',
    text: 'Connect Hai to any of your Bluetooth audio sources.',
  },
  {
    header: 'Cancel the buzz',
    text: "Hai's DBC technology clear the buzzing sound.",
  },
  {
    header: 'Enjoy',
    text: "Hai's neckcoil send a high quality audio signal to your hearing aids or cochlear implants.",
  },
];

class ImgSwap extends React.Component {
  constructor(props) {
    super(props);
    this.switchImage = this.switchImage.bind(this);
    this.state = {
      currentImage: 0,
      show: false,
      images: [
        step1,
        step2,
        step3
      ]
    };
  }

  switchImage() {
    if (this.state.currentImage < this.state.images.length - 1) {
      this.setState({
        currentImage: this.state.currentImage + 1, 
      });
    } else {
      this.setState({
        currentImage: 0
      });
    }
    return this.currentImage;
  }

  /*switchFade() {
    this.setState({ 
      show: !this.state.show, 
    });
    return this.show;
  }*/

  componentDidMount() {
    setInterval(this.switchImage, 500);
    setInterval(this.switchFade, 500);
  }

  render() {
    return (
      <div>
        <Fade>
          <img src={this.state.images[this.state.currentImage]}
            alt="animation images for introducing the usage of Hai"
          />
        </Fade>
      </div>
    );
  }
}

const Manual = () => (
  <StaticQuery
    query={graphql`
      query {
        Usage: file(
          sourceInstanceName: { eq: "photos" }
          name: { eq: "usage" }
        ) {
          childImageSharp {
            fluid(maxWidth: 1450) {
              ...GatsbyImageSharpFluid
            }
          }
        },
      }
    `}
    render={data => (
      <Section id="usage" accent="primary" padding="tight">
        <SectionHeader 
          header="How to use" 
          subheader=""
          bg="inverse"
        />
        <Container>
          <Wrapper>
            <CoverTextLayer>
              <Grid>
                <CoverText>
                    <Fade>
                      <p>① <b> {STEPS[0].header}</b><br/>
                      {STEPS[0].text}</p>
                    </Fade>
                    <Fade>
                      <p>② <b> {STEPS[1].header}</b><br/>
                      {STEPS[1].text}</p>
                    </Fade>
                    <Fade>
                      <p>③ <b> {STEPS[2].header}</b><br/>
                      {STEPS[2].text}</p>
                    </Fade>
                </CoverText>
              </Grid>
            </CoverTextLayer>
            <CoverImgLayer>
              <Fade>
                <ImgSwap/>
              </Fade>
            </CoverImgLayer>
            <MainImg>
              <Img fluid={data.Usage.childImageSharp.fluid} />
            </MainImg>
          </Wrapper>
        </Container>
      </Section>
    )}
  />
);



const Wrapper = styled.div`
  padding: 60px 0 40px 0;
  font-weight: 400;
  vertical-align: middle;
  color: ${props => props.theme.color.grayscale.S1000};
  width: 100%;
  @media (max-width: ${props => props.theme.screen.md}) {
    max-width: 100%;
    padding: 0 0 60px 0;
  }
  @media (max-width: ${props => props.theme.screen.sm}) {
    padding: 0 0 40px 0;
  }
  @media (max-width: ${props => props.theme.screen.xs}) {
    padding: 0 0 0 0;
  }
`;

const CoverTextLayer = styled.div`
  position: relative;
  z-index: 3;
  height: 0;
  left: -50px;
  top: 0px;
  width: calc(100% + 100px);
  color:  ${props => props.theme.color.grayscale.S900};
  @media (max-width: ${props => props.theme.screen.md}) {
    z-index: 1;
    height: auto;
    left: 0;
    width: 100%;
    color:  ${props => props.theme.color.grayscale.S100};
  }
`;

const CoverImgLayer = styled.div`
  position: relative;
  z-index: 2;
  height: 0;
  left: -50px;
  top: 0px;
  width: calc(100% + 100px);
  @media (max-width: ${props => props.theme.screen.md}) {
    z-index: 2;
    height: 0;
  }
`;


const Grid = styled.div`
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 10px;
  text-align: left;
  align-items: top;
  justify-items: left;
  @media (max-width: ${props => props.theme.screen.md}) {
    grid-template-columns: 1fr;
    text-align: left;
    margin-bottom: 96px;
    grid-gap: 15px;
    &:last-child {
      margin-bottom: 24px;
    }
  }
`;

const CoverText = styled.div`
  margin: 0;
  padding: 15px 20px;
  background-color:  ${props => props.theme.color.grayscale.S100};
  border:  ${props => props.theme.color.background.S300} 1px solid;
  @media (max-width: ${props => props.theme.screen.md}) {
    margin: 0;
    padding: 0;
    background-color: transparent;
    border: none;
  }
`;

const MainImg = styled.div`
  position: relative;
  z-index: 1;
  left: -50px;
  top: 0px;
  width: calc(100% + 100px);
`;


export default Manual;
